/* Core
-------------------------------------------------- */

@import "../../../node_modules/bootstrap/scss/functions";

/* custom variables*/
@import "variables-custom.scss";

/* bootstrap variables*/
@import "../../../node_modules/bootstrap/scss/variables";

/* components*/
@import "components.scss";

/* Project Fonts
-------------------------------------------------- */

/* overpass-100 - latin */
@font-face {
    font-family: "Overpass";
    font-display: swap;
    font-style: normal;
    font-weight: 100;
    src: local("Overpass Thin"), local("Overpass-Thin"), url("../fonts/overpass-v2-latin-100.woff2") format("woff2"),
        url("../fonts/overpass-v2-latin-100.woff") format("woff");
    /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* overpass-800 - latin */
@font-face {
    font-family: "Overpass";
    font-display: swap;
    font-style: normal;
    font-weight: 800;
    src: local("Overpass ExtraBold"), local("Overpass-ExtraBold"),
        url("../fonts/overpass-v2-latin-800.woff2") format("woff2"),
        url("../fonts/overpass-v2-latin-800.woff") format("woff");
    /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* Project functions
-------------------------------------------------- */

@function get-color($colorName) {
    @return map-get($colors, $colorName);
}

@function em2px($target, $context: 1em) {
    @if $target==0 {
        @return 0;
    }

    @return ($target / 1em) * ($context / 1em) * 16+0px;
}

/* Project variables
-------------------------------------------------- */

$mainTitleColor: danger-500;
$secondaryTitleColor: gray-400;

/* Project styles
-------------------------------------------------- */

html {
    position: relative;
    min-height: 100%;
}

h1,
h2 {
    font-family: "Overpass", sans-serif;
    font-weight: 100 !important;

    small {
        font-size: 50%;
    }
}

h3,
.h3,
h4,
.h4,
h5,
.h5,
h6 {
    font-family: "Overpass", sans-serif;
    text-transform: lowercase;
    font-variant: small-caps;
    font-weight: 800 !important;
}

h3 {
    &:not(.h4) {
        color: get-color($mainTitleColor);
        padding: 4px 12px;
        @extend .mb-5;
        border-bottom: 1px solid get-color($mainTitleColor);
    }

    .small {
        font-variant: normal;
    }
}

h4,
.h4,
h5,
.h5,
h6 {
    color: get-color($secondaryTitleColor);
}

figure {
    width: 100%;
    position: relative;
}

img[ci-src] {
    opacity: 0;
}

.img-thumbnail {
    box-sizing: content-box;
}

p.lead {
    font-weight: 400;
    color: get-color(gray-600);
}

pre,
code,
code[class*="language"] {
    font-size: 0.875rem;
}

strong {
    font-weight: $font-weight-bold;
    color: get-color(gray-800);
}

.container > section {
    margin-bottom: 200px;

    > article {
        margin-bottom: 100px;

        > section {
            margin-bottom: 100px;
        }
    }
}

section ul:not(.list-unstyled):not(.tree-list):not(.list-group):not(.pagination) {
    > li {
        position: relative;
        list-style: none;
        margin-bottom: 0.5rem;

        strong {
            color: get-color(gray-800);
        }

        ul > li {
            &:before {
                content: " ";
                width: 4px;
                height: 4px;
                background: get-color(gray-300);
                border-radius: 0;
                display: inline-block;
                position: absolute;
                left: -30px;
                top: 0.75em;
            }
        }

        &:before {
            content: " ";
            width: 6px;
            height: 6px;
            background: get-color(gray-400);
            display: inline-block;
            position: absolute;
            left: -30px;
            top: 0.55em;
        }
    }
}

ol li {
    @extend .mb-3;
}

.badge {
    border-radius: none !important;
}

.badge-light {
    @extend .bg-gray-100;
}

/* Main navbar
-------------------------------------------------- */

#website-navbar {
    z-index: 1040;

    .navbar-brand {
        min-width: 56px;
    }

    .navbar-nav .nav-link {
        display: block;
        font-weight: 500;

        &:hover,
        &:focus {
            text-decoration: none;
            color: get-color(gray-700);
        }
    }
}

/* Cards
-------------------------------------------------- */

.card-title {
    font-weight: 500;
    color: get-color(danger-500);
}

/* Breadcrumbs
-------------------------------------------------- */

.breadcrumb {
    .breadcrumb-item {
        font-size: 0.875rem;
        margin-bottom: 0;
    }
}

/* Numbered lists
-------------------------------------------------- */

.numbered {
    position: relative;
    counter-reset: my-counter;
    list-style: none;
    padding-left: 40px !important;
}

.numbered > dt,
.numbered > li {
    &:before {
        content: counter(my-counter);
        counter-increment: my-counter;
        position: absolute;
        left: 0;
        @extend .bg-gray;
        color: #fff;
        width: 1.5em;
        height: 1.5em;
        font-weight: normal;
        display: inline-block;
        text-align: center;
        border-radius: 50%;
    }

    &[class^="col-"]:before {
        left: -25px;
    }
}

/* Horizontal definition lists
-------------------------------------------------- */

dt {
    font-weight: 500;
}

dl.dl-horizontal {
    display: table;
    table-layout: fixed;
    /* min-width: 400px;*/
    margin-bottom: 60px;

    dt,
    dd {
        width: auto;
        overflow: visible;
    }

    dt {
        display: table-cell;
        text-align: right;
        white-space: nowrap;
        font-weight: 500;
        padding: 7px 20px 7px 0;

        > pre {
            margin-top: 3px;
        }
    }

    dd {
        display: table-cell;
        padding: 7px 0;

        &.line-break {
            display: table-row;
        }
    }
}

@media (min-width: 936px) {
    .dl-horizontal dt {
        min-width: 220px;
    }
}

/* Project Classes
-------------------------------------------------- */

#quick-access .img-overlay {
    width: 508px;
    height: 258px;
}

.bootstrap-theme-thumbnail {
    width: 120px;
    height: 90px;
}

@include media-breakpoint-up(md) {
    #quick-access .img-overlay {
        width: 328px;
        height: 166.582677px;
    }
}

@include media-breakpoint-up(lg) {
    #quick-access .img-overlay {
        width: 288px;
        height: 146.267717px;
    }
}

@include media-breakpoint-up(xl) {
    #quick-access .img-overlay {
        width: 348px;
        height: 176.740157px;
    }
}

.ndd-annotator-navigator-window {
    background: transparentize(get-color(blue-500), 0.5) !important;
}

/* background md, sm & xs devices*/

.bg-light {
    background: $gray-100 !important;
}

.border-dashed {
    border-style: dashed;
}

.has-separator {
    display: block;
    @extend .mb-5;
    position: relative;

    &:before,
    &:after {
        position: absolute;
        left: 50%;
        height: 1px;
        content: "";
        background: get-color(gray-300);
    }

    &:before {
        bottom: -em2px(1em);
        width: 12%;
        margin-left: -6%;
    }

    &:after {
        bottom: -em2px(1em) + 3px;
        width: 20%;
        margin-left: -10%;
    }
}

i[class*="fa-"],
i[data-fa-i2svg],
i > svg {
    display: inline-block !important;
    min-width: 1.125em !important;
}

.list-group-thumbnails li {
    width: 120px;
}

.mh-100vh {
    min-height: 100vh;
}

.nowrap {
    white-space: nowrap;
}

.phpcg-screenshot {
    max-width: 705px;
    @extend .mx-auto;
    @extend .my-5;
    height: auto;
}

.text-light {
    @extend .text-gray;
}

.underline {
    text-decoration: underline !important;
}
